.progress-bar {
  &.color-nodejs {
    &, &:after{
      background-color: #73b958;
    }
    span {
      color: #73b958;
    }
  }
  &.color-angular {
    &, &:after{
      background-color: #dd0031;
    }
    span {
      color: #dd0031;
    }
  }
  &.color-react, &.color-reactnative {
    &, &:after{
      background-color: #61dafb;
    }
    span {
      color: #61dafb;
    }
  }
  &.color-php {
    &, &:after{
      background-color: #8892BF;
    }
    span {
      color: #8892BF;
    }
  }
  &.color-python {
    &, &:after{
      background-color: #2b5b84;
    }
    span {
      color: #2b5b84;
    }
  }
  &.color-ionic {
    &, &:after{
      background-color: #4a8bfc;
    }
    span {
      color: #4a8bfc;
    }
  }
  &.color-phonegap {
    &, &:after{
      background-color: #27a1c5;
    }
    span {
      color: #27a1c5;
    }
  }
  &.color-nosql {
    &, &:after{
      background-color: #13aa52;
    }
    span {
      color: #13aa52;
    }
  }
  &.color-aws {
    &, &:after{
      background-color: #e15500;
    }
    span {
      color: #e15500;
    }
  }
  &.color-docker {
    &, &:after{
      background-color: #2491e5;
    }
    span {
      color: #2491e5;
    }
  }
  &.color-kubernetes {
    &, &:after{
      background-color: #326ce5;
    }
    span {
      color: #326ce5;
    }
  }
  &.color-html {
    &, &:after{
      background-color: #dc4a25;
    }
    span {
      color: #dc4a25;
    }
  }
  &.color-css {
    &, &:after{
      background-color: #264bdd;
    }
    span {
      color: #264bdd;
    }
  }
}

.flex-control-nav.flex-control-paging, .flex-direction-nav {
  display: none;
}

.project {
  box-shadow: 0px 0px 3px #dadada;
}
.hire {
  color: #fff;
  background: #251f13;
  .btn-hire {
    color: #fff;
    border-color: #fff;
  }
}
#colorlib-aside {
  background: linear-gradient(90deg, #ffffff 10.51%, #f1f1f1 70.95%, #b7b0b0 169.82%);
}
#colorlib-hero .flexslider {
  .slides li {
    background-position: center;
  }
  .slider-text {
    float: right;
    background: rgba(0, 0, 0, 0.4);
    & > .slider-text-inner {
      h1, h2 {
        color: #fff;
        font-weight: bold;
        /* span.fullname{
          background: linear-gradient(90deg ,#4966ff 0%,#7760ff 26%,#b149ff 58%,#f450ee 88%,#f450a6 100%);
          color: transparent;
          -webkit-background-clip: text;
          -webkit-text-fill-color: transparent;
        } */
      }

      .btn.btn-learn {
        color: #fff;
        border-color: #fff;
      }
    }
  }
}
.colorlib-counters .colorlib-counter-label {
  font-weight: bold;
}

i.icon-angular {
  background: url('./icons/icon-angular.svg') no-repeat center;
  background-size: 100%;
}
i.icon-nodejs {
  background: url('./icons/icon-nodejs.svg') no-repeat center;
  background-size: 100%;
}
i.icon-react {
  background: url('./icons/icon-react.svg') no-repeat center;
  background-size: 100%;
}

.services.color-5 {
  .icon {
    background: #c8e4bd;
    &:before {
      border-color: transparent transparent #c8e4bd transparent;
    }
    &:after {
      border-color: #c8e4bd transparent transparent transparent;
    }
  }
}
.services.color-4 {
  .icon {
    background: #af1f3f;
    &:before {
      border-color: transparent transparent #af1f3f transparent;
    }
    &:after {
      border-color: #af1f3f transparent transparent transparent;
    }
  }
}
.services.color-6 {
  .icon {
    background: #459cb3;
    &:before {
      border-color: transparent transparent #459cb3 transparent;
    }
    &:after {
      border-color: #459cb3 transparent transparent transparent;
    }
  }
}

@media screen and (max-width: 768px) {
  #colorlib-hero, #colorlib-counter {
    margin-left: -1em;
    margin-right: -1em; 
    width: initial;
  }
}